<template>
    <div class="outer-container">
        <div class="inner-container">
            <toolbar/>
            <div class="story-card-container">
                <story :story="this.story"/>
            </div>
            <div class="children-container">
                <story v-for="story in children" v-bind:key="story._id" :story="story"/>
            </div>
        </div>
    </div>
</template>

<script>
import Story from '../molecules/Story.vue';
import Toolbar from '../atoms/Toolbar.vue';
const api = "https://tetherapi.herokuapp.com/";
export default {
    name:'story-view',
    props: ['username', 'story_id', 'story'],
    components: {Story, Toolbar},
    data: function() {
        return {
            children: []
        }
    },
    mounted: function() {
        if(this.story === undefined) {
            this.getStory(this.story_id);
        }
        this.getChildren(this.story_id);
    },
    beforeRouteUpdate (to, from, next) {
        // just use `this`
        this.children = []
        this.getStory(to.params.story_id)
        this.getChildren(to.params.story_id)
        next()
    },
    methods: {
        getStory: function(id) {
            this.$http.get( api + 'stories/' + id).then( response => {
                console.log(response.body);
                this.story = response.body
            }, error => {
                //error
                console.log(error);
            });
        },
        getChildren: function(id) {
            this.$http.get( api + 'children/' + id).then( response => {
                console.log(response.body);
                this.children = response.body;
            }, error => {
                //error
                console.log(error);
            });
        }
    }

}
</script>

<style lang="scss">
@import '../../styles/styles.scss';

.outer-container {
    height: 100%;
    width: 100%;

    .inner-container {
        height: 100%;
        width: 100%;
        background: $background;
        display: flex;
        flex-direction: column;
        align-items: center;

        .task-bar {
            height: 2em;
            width: 100%;
            background-color: $accent-grey;
        }
        .story-card-container {
            width: 100%;

            .card-container {
                margin: 0em;
            }
            // width: $mobile-card-width;
            // @include desktop {
            //     width: $desktop-card-width;
            // }
        }
        .children-container {
            width: $mobile-card-width;
            @include desktop {
                width: $desktop-card-width;
            }
        }
    }
}
</style>


